























export default {
	data() {
		return {
			type: 0//认证类型  企业1/团队2
		}
	},
	onLoad(partemater) {
		this.type = parseInt(partemater.type);
	},
	methods: {
		agree() {
			if(this.type === 1){
				uni.navigateTo({
					url: './companyverify'
				});
			}else if(this.type === 2){
				uni.navigateTo({
					url: './companyverify'
				});
			}
		},
		disagree(){
			uni.navigateBack();
		}
	},
}
